import { createRouter, createWebHistory } from 'vue-router'
import GanhoView from '../views/GanhoView.vue'
import CadastroGanhoView from '../views/CadastroGanhoView.vue'
import RegistroGanhoView from '../views/RegistroGanhoView.vue'

import GastoView from '../views/GastoView.vue'
import CadastroGastoView from '../views/CadastroGastoView.vue'
import RegistroGastoView from '../views/RegistroGastoView.vue'



import GaloesView from '../views/GaloesView.vue'
import GaloesCadastroView from '../views/GaloesCadastroView.vue'
import RegistroGaloesView from '../views/RegistroGaloesView.vue'

import FiadoView from '../views/FiadoView.vue'
import FiadoCadastroView from '../views/FiadoCadastroView.vue'
import RegistroFiadoView from '../views/RegistroFiadoView.vue'

const routes = [
  {
    path: '/ganho',
    name: 'ganho',
    component: GanhoView
  },
  {
    path: '/faturamento',
    name: 'faturamento',
    component: CadastroGanhoView
  },
  {
    path: '/registroganho',
    name: 'registroganho',
    component: RegistroGanhoView
  },

  {
    path: '/gasto',
    name: 'gasto',
    component: GastoView
  },
  {
    path: '/cadastrogasto',
    name:'cadastrogasto',
    component: CadastroGastoView
  },
  {
    path: '/registrogasto',
    name: 'registrogasto',
    component: RegistroGastoView
  },

  {
    path: '/galoes',
    name: 'galoes',
    component: GaloesView
  },
  {
    path: '/cadastrogaloes',
    name: 'cadastrogaloes',
    component: GaloesCadastroView
  },
  {
    path: '/registrogaloes',
    name: 'registrogaloes',
    component: RegistroGaloesView
  },

  {
    path: '/fiado',
    name: 'fiado',
    component: FiadoView
  },
  {
    path: '/cadastrofiado',
    name: 'cadastrofiado',
    component: FiadoCadastroView
  },
  {
    path: '/registrofiado',
    name: 'registrofiado',
    component: RegistroFiadoView
  }

]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
