<template>
    <div class="principal">
    <h1>Registro de Galoes</h1>

    <div id="home">
  
     <table class="table">
  <thead>
    <tr>
      <th>Nome</th>
      <th>Quantidade</th>
      <th style="padding-left: 55px;">Ações</th>
    </tr>
  </thead>
  <tbody >
    <tr v-for="galao in galoes" :key="galao.id">
        <td>{{galao.nome}}</td>
        <td>{{galao.quantidade}}</td>
         <!--  <button class="button is-success">Editar</button> -->
        <button class="button is-danger" @click="showModalGanho(galao.id)">Deletar</button>

    </tr>
  
  </tbody>
  </table>

     <div :class="{modal: true, 'is-active': showModal}" >
        <div class="modal-background"></div>
        <div class="modal-content">
             <div class="card">
    
        <header class="card-header">
           <!-- <p class="card-header-title">
            Você quer realmente deleter este usuário ?
            </p> -->
        </header>
        <div class="card-content">
            <div class="content">
                <p>Você quer realmente deleter ?</p>
            </div>
        </div>
        <footer class="card-footer">
            <a href="#" class="card-footer-item" @click="hideModal()">Cancelar</a>
            <a href="#" class="card-footer-item" @click="deleteGalao()">Sim, quero deletar!</a>
        </footer>
        </div>

        </div>
        <button class="modal-close is-large" aria-label="close" @click="hideModal()"></button>
    </div>

   </div>
 </div>
</template>


<script>
import axios from 'axios'
export default {
    created(){
        axios.get("http://134.122.18.130/galoes").then(res =>{
            console.log(res)
            this.galoes = res.data
        }).catch(err =>{
            console.log(err)
        })

    },
     data(){
        return {
            galoes: [],
            showModal: false,
            deleteGalaoId: -1
        }
    },
 methods: {
        hideModal(){
            this.showModal = false;
        },
        showModalGanho(id){
            this.deleteGalaoId = id;
            this.showModal = true;
            console.log("id" +id)
        },
        deleteGalao(){
        axios.delete("http://134.122.18.130/galoes/"+this.deleteGalaoId).then(res =>{
            console.log(res)
            this.showModal = false
            this.galoes = this.galoes.filter(g => g.id != this.deleteGalaoId)
        }).catch(err =>{
            console.log(err)
            this.showModal = false
        })

     }
   }
    
}



</script>

<style scoped>
#home{
    display: flex;
    align-items: center;
    justify-items: center;
    margin: auto;
    margin-top: 25px;
    padding: 10px;
    background-color: rgb(0, 0, 0);
    width: 50%;
}

h1{
    text-align: center;
}

.table{
    width: 100%;
}

button{
  padding: 10px;
  margin-right: 10px;
  margin: 10px;
  border-radius: 10px;
}

.principal{
    background-color: black;
    border: 3px solid #73AD21;
}

h1{
    text-align: center;
    color: white;
    font-weight: bold;
    padding-top: 15px;
}


@media screen and (max-width: 830px){
button{
    width: 55px;
    height: 55px;
}

.table{
    width: 100%;
}

#home{
    width: 100%;

    }

}


</style>