<template>
<div class="principal">

    <h1>Registro de Ganho</h1>
    <hr>


      <div class="columns is-mobile">
        <div class="column is-half is-offset-one-quarter">
        <div v-if="error != undefined" >
         <div class="notification is-danger">
            <p>{{error}}</p>
         </div>
        </div>
       
        <label for="nome">Nome:</label>
         <input type="text" placeholder="Digite o Nome ?" class="input" v-model="nome"> <br><br>
        <label for="nome">Valor:</label>
         <input type="text" placeholder="Digite o Valor ?" class="input" v-model="valor"> <br><br>
        <button class="button is-primary is-large" @click="register" >Cadastrar</button>
    </div>
    </div>

</div>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return{
            nome: '',
            valor:'',
            error: undefined
        }
    },
    methods: {
     register(){
        axios.post("http://134.122.18.130/ganhos",{
            nome: this.nome,
            valor: this.valor
        }).then(res =>{
            console.log(res)
            this.$router.push({name: 'registroganho'})
        }).catch(err =>{
            var msgErro = err.response.data.err
            this.error = msgErro
        })
    }
    }
}
</script>


<style scoped>

.principal{
    background-color: black;
    border: 3px solid #73AD21;
}

h1{
    text-align: center;
    color: white;
    font-weight: bold;
    padding-top: 15px;
}

label{
    text-align: center;
    color: white;
    font-weight: bold;
    padding-top: 15px; 
}

input{
    text-align: center;
    color: rgb(0, 0, 0);
    font-weight: bold;
    padding-top: 15px;
}

button{
    margin-bottom: 10px;
}


</style>